import Foo from "./views/Foo";
import Api from "./utils/api";
export default {
  components: {
    Foo: Foo
  },
  data: function data() {
    return {
      label: "作业收集系统",
      container_style: {
        margin: "0 auto",
        height: "100%"
      },
      header_height: {
        padding: 0,
        backgroundColor: "#545c64",
        height: "6%"
      },
      aside_width: {
        backgroundColor: "#545c64",
        padding: 0,
        width: "200px"
      },
      admin: false
    };
  },
  methods: {},
  computed: {},
  created: function created() {
    var _this = this;

    this.$router.push("/upload");

    if (window.innerWidth < 600) {
      this.aside_width.width = "0px";
    }

    Api.check_token().then(function (data) {
      _this.admin = data.code === 200;
    });
  }
};